/* ===
Global styling 
=== */

* {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  color: #333;
}

body {
  font-family: 'Roboto','Helvetica','Arial',sans-serif;
  background: #FFFDFA;
}

ul {
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  font-size: 16px;
  font-weight: 400;
}

input {
  font-family: 'Roboto','Helvetica','Arial',sans-serif;
}

button {
  font-family: 'Roboto','Helvetica','Arial',sans-serif;
}

/* ===
App
=== */
.logo {
  font-weight: 600;
  font-size: 32px;
}

.App {
  text-align: center;
  margin-top: 5%;
}

/* ===
CitySearch 
=== */
input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city-search {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 700px;
  margin:20px auto;
  position: relative;
}
.Alert {
  height: 20px;
}
.city {
  width: 250px;
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 60px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.5);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #fafafa;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #ffc0f1;
}

/* ===
Event 
=== */
.EventList {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.event {
  width: 550px;
  margin: 20px 0 0 20px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.event-title {
  background-color: #d328ae;
  border: 1px solid #d328ae;
  border-radius: 4px 4px 0 0;
  padding: 10px;
}

.event-summary {
  text-align: center;
  font-size: 24px;
  color: #fff;
}

.event-content {
  padding: 15px 20px;
  background: #fff;
  margin: 0;
}

.event-display {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.event-display div {
  margin: 0;
}

.event-location {
  font-size: 18px;
  margin-bottom: 5px;
}

.event-date {
  font-size: 14px;
  margin: 7px 0;
}

.event-description-title {
  font-weight: 600;
  margin: 10px 0 7px 0;
}

.event-description {
  line-height: 1.3;
}

.toggle-btn {
  height: 40px;
  width: 100px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
  background-color: #fff;
  color: #d328ae;
  border: 1px solid #d328ae;
  margin: 0;
  transition: 0.6s;
}

.toggle-btn:hover {
  background-color: #d328ae;
  color: #fff;
}


