.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #d328ae;
  border-radius: 4px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  transition: 0.5s;
}

.google-btn button {
  border: none;
  border-radius: 0 4px 4px 0;
  transition: 0.5s;
}

.google-btn:hover {
  box-shadow: 0 0 6px #d328ae;
}

.google-btn:active {
  background: #ca15a3;
}

.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}

.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: 'Roboto';
  background-color: #d328ae;
  width: 80%;
  height: 100%;
  text-align: center;
  transition: 0.5s;
}

.btn-text:hover {
  cursor: pointer;
}

.privacy-policy {
  transition: 0.5s;
}

.privacy-policy:hover {
  color: #d328ae;
}
